export const UsersPage = () => {
  const [selectedTab, setSelectedTab] = useState(0);
  // const [sideNav, setSideNav] = useState('information');
  const classes = useStyles();

  const handleTabChange = (
    _event: React.ChangeEvent<HTMLDivElement>,
    tab: number,
  ) => {
    setSelectedTab(tab);
  };

  return (
    <div className={classes.main}>
      <Grid
        container
        justify="space-between"
        alignItems="center"
        className={classes.gridContainer}
      >
        <Grid item>
          <Typography className={classes.headerTitle}>Users
        <UsersTabs
              value={selectedTab}
              onChange={handleTabChange}
              aria-label="page tab"
            >
              {page_tab_titles.map((title: string, index: number) => (
                <UsersTab
                  label={title}
                  id={'users_tab_' + index}
                  aria-controls={'users-tabpanel-' + index}
                  key={index}
                />
              ))}
            </UsersTabs>
          </Typography>
        </Grid>
      </Grid>

      {/* Headers */}


      {/* Body */}
      <Grid container>
        <Grid item xs={12} md={12} className={classes.selectedpage}>
          {
            selectedTab === 0 ?
              (<AccountTabPage />)
              : selectedTab === 1 ?
                (<RolesTableList classes={classes} />)
                : null
          }
          {/* <UsersTable classes={classes} /> */}
        </Grid>
      </Grid>


    </div>
  );
};

const mapStateToProps = (state: Store) => state.users;

const mapDispatchToProps = (dispatch: Dispatch) => ({
  Map: bindActionCreators(UsersActions.Map, dispatch),
});

export { mapStateToProps, mapDispatchToProps };
