import React from 'react'
import { TableComponent } from './TableComponent'

import { userTableColumn, tableConst } from './Accounts.const'
import { getUserList, searchUser } from 'Pages/API/Calls/User'
import { UI } from 'Components'
import { 
    Grid,
} from '@material-ui/core';
import { ReactSearch } from 'Components/UI/SearchBar/ReactSearch';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPlus } from '@fortawesome/free-solid-svg-icons';
import Link from '@material-ui/core/Link';
import { Link as RouteTo } from 'react-router-dom'
import { makeStyles } from '@material-ui/core/styles';
import { Redirect } from 'react-router-dom'
import moment from 'moment';

interface IProps {

}


const useStyles = makeStyles({
    addFieldIcon: {
        color: '#3AB77D',
        cursor: 'pointer',
        fontSize: '13px',
        marginRight: '8px',
        marginLeft: '20px'
    },
    searchSection: {
        marginBottom: '21px'
    },
    label: {
        color: '#272E4C',
        fontSize: 13
    },
    value: {
        color: '#3AB77D',
        fontSize: 13,
        fontWeight: 800,
        marginRight: '10px'
    },
    tableTop: {
        padding: '20px'
    },
    searchTabAction: {
        display: 'flex',
        justifyContent: 'flex-end'
    }
})

export const AccountTabPage: React.FC<IProps> = (props: IProps): JSX.Element => {
    const classes = useStyles();
    React.useEffect(() => {
        getUsers({pageSize: 10, offset: 0}, true)
    }, [])

    const {

    } = props;

    // Fetch
    const getUsers = (newState?: any, saveSummary?: boolean) => {
        setLoading(true)
        getUserList(
            newState.pageSize,
            newState.offset,
            newState.where,
            newState.order).then((data: any) => {

            if (!data.error) {
                const state = {
                    ...newState,
                    rows: data.body,
                    totalCount: data.count
                }

                updateTableState(state)

                if (saveSummary && saveSummary === true) {
                    setSummary(data.summary)
                }
            } else {
                const state = {
                    ...newState,
                    rows: [],
                    totalCount: 0
                }

                updateTableState(state)
            }
           
            setLoading(false)
        }).catch(() => {
            setLoading(false)
        })
    }

    // Fetch
    const getSearchUser = (newState?: any) => {
        setLoading(true)
        searchUser(
            newState.pageSize,
            newState.offset,
            newState.query,
            newState.where,
            newState.order,).then((data: any) => {

            if (!data.error) {
                const state = {
                    ...newState,
                    rows: data.body,
                    totalCount: data.count
                }

                updateTableState(state)
            } else {
                const state = {
                    ...newState,
                    rows: [],
                    totalCount: 0
                }

                updateTableState(state)
            }
           
            setLoading(false)
        }).catch(() => {
            setLoading(false)
        })
    }

    // Table event handlers
    const onCurrentPageChange = (value) => {
        const state = {
            currentPage: value,
            pageSize: tableStates.pageSize,
            offset: tableStates.pageSize * value,
            order: tableStates.order,
            where: tableStates.where
        }

        getUsers(state)
    }

    const onPageSizeChange = (value) => {
        const state = {
            pageSize: value,
            offset: tableStates.offset,
            order: tableStates.order,
            where: tableStates.where
        }

        getUsers(state)
    }

    const onFilterChange = (value) => {
        if (typingTimeout) {
            clearTimeout(typingTimeout);
        }

        setTypingTimeout (setTimeout(function () {
            const state = {
                pageSize: tableStates.pageSize,
                offset: 0,
                order: tableStates.order,
                where: {}
            }
    
            value.map(w => 
                w.columnName === 'name' ?
                    state.where = {
                        or: [
                            {
                                first_name: {
                                    like: `${w.value}.*`,
                                    options: 'i'
                                }
                            },{
                                middle_name: {
                                    like: `${w.value}.*`,
                                    options: 'i'
                                }
                            },{
                                last_name: {
                                    like: `${w.value}.*`,
                                    options: 'i'
                                }
                            }
                        ]
                    }
                :  w.columnName === 'group' ?
                    state.where['group_name'] = {
                        like: `${w.value}.*`,
                        options: 'i'
                    }
                :  w.columnName === 'role' ?
                    state.where[ "role.name"] = {
                        like: `${w.value}.*`,
                        options: 'i'
                    }
                :
                state.where[w.columnName] = {
                    like: `${w.value}.*`,
                    options: 'i'
                }
            )

            getUsers(state)
        }, 3000));
    }

    const onSortingChange = (value) => {
        let a = Object.values(value[0]).join(' ')
        
        const state = {
            pageSize: tableStates.pageSize,
            offset: tableStates.offset,
            order: a,
            where: tableStates.where
        }

        getUsers(state)
    }

    const onRowClick = (value) => {
        setUserIdSelected(value)
        setIsRowClicked(true)
    }

    // const handleSearchChange = ({target}) => {
    //     setSearchValue(target.value)
    // }

    const updateTableState = (prop) => {
        setTableState({
            ...tableStates,
            ...prop
        })
    }

    const rowDataFixer = (array: any) => {
        let newEntry: any = [];
        if (array.length > 0) {
            array.forEach(data => {
                let newRow = {
                    ...data,
                    name: `${data.first_name} ${data.middlename} ${data.last_name}`,
                    group: data.hasOwnProperty('group') ? data.group.name : 'N/A',
                    role: data.hasOwnProperty('role') && data.role.length > 0 ? data.role.map((x, i) => `${x.name}${i !== (data.role.length - 1) ? ', ' : ''}`) : 'N/A',
                    expiry_date: data.hasOwnProperty('expiry_date') ? moment(data.expiry_date).format('MMM DD, YYYY') : 'N/A',
                    last_login: data.hasOwnProperty('last_login') ? moment(data.last_login).format('MMM DD, YYYY') : 'N/A',
                    status: data.hasOwnProperty('status') ? <span style={{textTransform: 'capitalize'}}>{data.status}</span> : 'N/A',
                }

                newEntry.push(newRow)
            })

            return newEntry
        }   

        return []
    }

    const handleSearchUser = (e) => {
        const state = {
            pageSize: tableStates.pageSize,
            offset: 0,
            order: tableStates.order,
            where: tableStates.where,
            query: e
        }

        if (e !== '') {
            getSearchUser(state)
        } else {
            getUsers(state)
        }

    }

    // States
    const [tableStates, setTableState] = React.useState<any>({
        rows: [],
        currentPage: 0,
        totalCount: 0,
        pageSize: 10,
        offset: 0,
        order: '',
        where: {},
        query: ''
    })

    // const [searchValue, setSearchValue] = React.useState<string>('')
    const [isLoading, setLoading] = React.useState<boolean>(true)
    const [isRowClicked, setIsRowClicked] = React.useState<boolean>(false)
    const [userIdSelected, setUserIdSelected] = React.useState<string>('')
    const [typingTimeout, setTypingTimeout] = React.useState<any>(0)
    // const [query, setQuery] = React.useState<string>('')
    const [summary, setSummary] = React.useState<any>()

    if (isRowClicked === true) {
        return <Redirect to={`accounts/${userIdSelected}`} />
    }
  
    return (
        <>
        { isLoading && <UI.LoadingIndicator /> }
            <Grid
                container xs={12}
                spacing={2}
                direction="row"
                justify="center"
                alignItems="center"
                className={classes.searchSection}>
                <Grid item xs={9}>
                    {/* <OutlinedInput
                        fullWidth={true}
                        id="outlined-adornment-amount"
                        value={query}
                        onChange={handleSearchUser}
                        placeholder="Search user"
                        startAdornment={
                        <InputAdornment position="start">
                            <SearchIcon style={{color: '#363636', opacity: 0.4}} />
                        </InputAdornment>}
                        labelWidth={0} /> */}
                    <ReactSearch placeholder={'Search user'} onSearch={handleSearchUser} />
                </Grid>
                <Grid
                    xs={3}
                    md={3}>
                    <div className={classes.searchTabAction}>
                        <UI.GenerateReport></UI.GenerateReport>
                        <FontAwesomeIcon icon={faPlus} className={classes.addFieldIcon} />
                        <RouteTo to='adduser'>
                            <Link>Add User</Link>
                        </RouteTo>
                    </div>
                </Grid>
            </Grid>
            <div style={{boxShadow: '0px 3px 6px #00000029'}}>
                <Grid container className={classes.tableTop}>
                    {
                        summary && Object.entries(summary).length > 0 ?
                            <>
                                <Grid
                                    style={{textAlign: 'center'}}
                                    item xs={3}>
                                    <span className={classes.value}>{summary.total_users}</span>
                                    <span className={classes.label}>TOTAL</span>
                                </Grid>
                                <Grid
                                    style={{textAlign: 'center'}}
                                    item xs={3}>
                                    <span className={classes.value}>{summary.onlineUsers}</span>
                                    <span className={classes.label}>ONLINE NOW</span>
                                </Grid>
                                <Grid
                                    style={{textAlign: 'center'}}
                                    item xs={3}>
                                    <span className={classes.value}>{summary.active_users}</span>
                                    <span className={classes.label}>ACTIVE</span>
                                </Grid>
                                <Grid
                                    style={{textAlign: 'center'}}
                                    item xs={3}>
                                    <span className={classes.value}>{summary.deactived_users}</span>
                                    <span className={classes.label}>INACTIVE</span>
                                </Grid>
                            </>
                        : 'Loading...'
                    }
                </Grid>
                <TableComponent
                    rows = {rowDataFixer(tableStates.rows)}
                    columns = {userTableColumn}
                    pageSize = {tableStates.pageSize}
                    pageSizes = {tableConst}
                    currentPage = {tableStates.currentPage}
                    totalCount = {tableStates.totalCount}
                    // Events
                    onRowClick = {onRowClick}
                    onFilterChange = {onFilterChange}
                    onCurrentPageChange = {onCurrentPageChange}
                    onPageSizeChange = {onPageSizeChange}
                    onSortingChange = {onSortingChange} />
            </div>
        </>
    )
} 