import React from 'react'

import {
    Grid,
    VirtualTable,
    TableHeaderRow,
    TableSelection,
    PagingPanel,
    TableFilterRow
} from '@devexpress/dx-react-grid-material-ui'

import {
    PagingState,
    CustomPaging,
    SortingState,
    SelectionState,
    FilteringState,
    // IntegratedFiltering
  } from '@devexpress/dx-react-grid'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { MuiPickersUtilsProvider, KeyboardDatePicker } from '@material-ui/pickers'
import MomentUtils from '@date-io/moment'
import {
    faSort,
    faSortUp,
    faSortDown
} from '@fortawesome/free-solid-svg-icons'
import * as MaterialUI from '@material-ui/core'

interface IProps {
    rows: any[]
    columns: any[]
    totalCount: number
    currentPage: number
    pageSize: number
    pageSizes: number[]
    // Evemts
    onCurrentPageChange: (value) => void
    onFilterChange: (value) => void
    onPageSizeChange: (value) => void
    onSortingChange: (value) => void
    onRowClick: (value) => void
}

const COLUMN_EXTENSIONS = [
  {
    columnName: 'username',
    wordWrapEnabled: true,
  },
  {
    columnName: 'name',
    wordWrapEnabled: true,
  },
  {
    columnName: 'group',
    wordWrapEnabled: true,
  },
  {
    columnName: 'role',
    wordWrapEnabled: true,
  },
];

const statusOption = [
  {
    value: 'active',
    label: 'Active'
  },
  {
    value: 'inactive',
    label: 'Inactive'
  },
  {
    value: 'locked',
    label: 'Locked'
  }
]

const Select = ({ items, ...restProps  }: any) => {
  return (
      <MaterialUI.Select
          variant='outlined'
          style={{ width: '100%' }}
          renderValue={(selected: any) => {
              if (selected != null) {
                  const value: any = items.find((item: any) => {
                      return item.value === selected;
                  });
                  if (value) {
                      return value.label;
                  } else {
                      return '';
                  }
              }
              return '';
          }}
          {...restProps}
      >
          {items.map((item: any, i: number) => (
              <MaterialUI.MenuItem key={i} value={item.value}>
                  {item.label}
              </MaterialUI.MenuItem>
          ))}
      </MaterialUI.Select>
  );
};

const FilterCell = ({ filter, onFilter, filteringEnabled, classes, column }: any) => {
  if (filteringEnabled) {
      if (column.name === 'last_login' || column.name === 'expiry_date') {
          return (
            <MaterialUI.TableCell style={{ padding: '0px 10px 8px 10px' }} classes={classes}>
              <MuiPickersUtilsProvider utils={MomentUtils}>
                <KeyboardDatePicker
                    disabled
                    variant="dialog"
                    format="MMMM DD, YYYY"
                    inputVariant="outlined"
                    // className={datePickerClasses.keyboardDatePicker}
                    value={filter ? filter.value : ''}
                    invalidDateMessage=""
                    onChange={date => {console.log(date); onFilter(date ? { value: date.toISOString() } : null)}}
                />
              </MuiPickersUtilsProvider>
            </MaterialUI.TableCell>
          )
      } else if (column.name === 'status') {
        return (
          <MaterialUI.TableCell classes={classes} style={{paddingTop: '5px'}}>
            <Select
                onChange={e => onFilter(e.target.value ? { value: e.target.value } : null)}
                name={'status'}
                items={statusOption}
                id={'status'}
                value={filter ? filter.value : ''}
            />
          </MaterialUI.TableCell>
        )
      } else {
          return (
              <MaterialUI.TableCell style={{ padding: '0px 10px 8px 10px' }} classes={classes}>
                  <MaterialUI.OutlinedInput
                      classes={classes}
                      style={{ width: '100%' }}
                      value={filter ? filter.value : ''}
                      labelWidth={0}
                      onChange={e => onFilter(e.target.value ? { value: e.target.value } : null)}
                      placeholder=""
                  />
              </MaterialUI.TableCell>
          );
      }
  }
  return null;
}

const SortingIcon = ({ direction }: any) =>
  direction === 'asc' ? (
    <FontAwesomeIcon style={{ marginLeft: '10px' }} icon={faSortUp} />
  ) : (
    <FontAwesomeIcon style={{ marginLeft: '10px' }} icon={faSortDown} />
  );

const renderSortable = ({ column, onSort, children, direction }: any) => {
  if (
      column.sortingEnabled !== undefined &&
      column.sortingEnabled !== null &&
      column.sortingEnabled === false
    ) {
      return <span>{column.title}</span>;
    } else {
      return (
        <MaterialUI.TableSortLabel hideSortIcon={true} onClick={onSort}>
          {children}
          {direction ? (
            <SortingIcon direction={direction} />
          ) : (
            <FontAwesomeIcon style={{ marginLeft: '10px' }} icon={faSort} />
          )}
        </MaterialUI.TableSortLabel>
      );
  }
}

export const TableComponent: React.FC<IProps> = (props: IProps): JSX.Element => {
    const {
        rows,
        columns,
        totalCount,
        currentPage,
        pageSize,
        pageSizes,
        // events
        onFilterChange,
        onCurrentPageChange,
        onPageSizeChange,
        onSortingChange,
        onRowClick
    } = props;

    const getRowId = row => row._id;

    const [selection, setSelection] = React.useState([]);

    const handleSelection = (e) => {
      setSelection(e);
      onRowClick(e);
    }

    return (
        <>
            <Grid
              getRowId={getRowId}
              rows={rows}
              columns={columns}>

              <FilteringState
                onFiltersChange={onFilterChange} />
              {/* <IntegratedFiltering /> */}

              <SelectionState
                selection={selection}
                onSelectionChange={handleSelection}
              />

              <SortingState onSortingChange={onSortingChange} />

              <PagingState
                  currentPage = {currentPage}
                  pageSize = {pageSize}
                  // Events
                  onCurrentPageChange = {onCurrentPageChange}
                  onPageSizeChange = {onPageSizeChange}
              />

              <CustomPaging totalCount = {totalCount}/>

              <VirtualTable
                columnExtensions={COLUMN_EXTENSIONS} />
              <TableHeaderRow showSortingControls sortLabelComponent={renderSortable}/>

              <TableFilterRow cellComponent={FilterCell} />

              <TableSelection
                selectByRowClick
                highlightRow
                showSelectionColumn={false}
              />

              <PagingPanel pageSizes = {pageSizes} />
            </Grid>
        </>
    )
}