import * as React from 'react';
// import { useState } from 'react';
import Paper from '@material-ui/core/Paper';
import {
    FilteringState,
    IntegratedFiltering,
    SortingState,
    IntegratedSorting,
    IntegratedGrouping,
    GroupingState,
    SelectionState,
    // IntegratedPaging,
    PagingState,
    CustomPaging,
} from '@devexpress/dx-react-grid';
import {
    Grid,
    Table,
    //   Toolbar,
    TableHeaderRow,
    TableFilterRow,
    TableGroupRow,
    TableSelection,
    PagingPanel,
    // TableFixedColumns,
} from '@devexpress/dx-react-grid-material-ui';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
    // faSearch,
    faCheckSquare,
    faMinusSquare,
    faSort,
    faSortUp,
    faSortDown,
    faMinusCircle,
    faAngleDown,
    faAngleUp,
    // faPlus,
    // faCheck, 
    // faTimes
} from '@fortawesome/free-solid-svg-icons';
import * as MaterialUI from '@material-ui/core';
import { Loader } from 'Pages/Lookup/Components/Loader';
// import { ReactSearch } from '../SearchBar/ReactSearch';

export interface IReactTableProps {
    rows: any,
    columns: any,
    disableSort?: boolean,
    disableFilter?: boolean,
    enableGrouping?: boolean,
    enableSelection?: boolean,
    enableSelectAll?: boolean,
    groupColumnName?: string,
    viewOnly?: boolean,
    columnExtensions?: any[],
    message?: string,
    sortingStateColumnExtensions?: any[],
    defaultSorting?: any[],
    totalCount?: number,
    currentPage?: number,
    pageSize?: number,
    groupNameChecked?: any,
    loading?: boolean,
    defaultFilters?: any,
    onDeleteRow?: (row) => void,
    onRowClick?: (row) => void,
    onPageChange?: (page) => void,
    onSetPageSize?: (size) => void,
    onCheckRow?: (row) => void,
    onSelectGroup?: (group) => void,
    onFilterChange?: (filter) => void,
    onSortingChange?: (sort) => void,

}

const defaultProps: IReactTableProps = {
    rows: [],
    columns: [],
    disableSort: false,
    disableFilter: false,
    enableGrouping: false,
    enableSelection: false,
    enableSelectAll: false,
    groupColumnName: '',
    viewOnly: true,
    columnExtensions: [],
    message: '',
    sortingStateColumnExtensions: [],
    defaultSorting: [],
    totalCount: 0,
    currentPage: 0,
    pageSize: 0,
    groupNameChecked: [],
    loading: false,
}

const FilterCell = ({ filter, onFilter, filteringEnabled, classes, column }: any) => {
    if (filteringEnabled) {
        if (column.name === 'action_delete') {
            return (
                <MaterialUI.TableCell style={{ padding: '0px 10px 8px 10px' }} classes={classes}>
                </MaterialUI.TableCell>
            )
        } else {
            return (
                <MaterialUI.TableCell style={{ padding: '0px 10px 8px 10px' }} classes={classes}>
                    <MaterialUI.OutlinedInput
                        classes={classes}
                        style={{ width: '100%' }}
                        value={filter ? filter.value : ''}
                        labelWidth={0}
                        onChange={e => onFilter(e.target.value ? { value: e.target.value } : null)}
                        placeholder=""
                    />
                </MaterialUI.TableCell>
            );
        }
    }
    return null;
}

const SortingIcon = ({ direction }: any) =>
    direction === 'asc' ? (
        <FontAwesomeIcon style={{ marginLeft: '10px' }} icon={faSortUp} />
    ) : (
            <FontAwesomeIcon style={{ marginLeft: '10px' }} icon={faSortDown} />
        );

const SortLabel = ({ column, onSort, children, direction }: any) => {
    if (column.name === 'action_delete') {
        return null;
    } else {
        return (
            <MaterialUI.TableSortLabel hideSortIcon={true} onClick={onSort} style={{ textOverflow: "ellipsis", overflow: "hidden" }}>
                {children}
                {direction ? (
                    <SortingIcon direction={direction} />
                ) : (
                        <FontAwesomeIcon style={{ marginLeft: '10px' }} icon={faSort} />
                    )}
            </MaterialUI.TableSortLabel>
        );
    }
};


const groupIconContentCell = ({expanded}: any) => {
    return (
        <>
            <div style={{padding: '8.5px 12px', display:'table-cell', verticalAlign:'middle'}} > 
                <FontAwesomeIcon 
                    icon={expanded ? faAngleUp : faAngleDown} 
                    size={'2x'}
                    color={"rgba(0, 0, 0, 0.54)"}
                />       
            </div>
        </>
    )
}



export const ReactTable: React.FC<IReactTableProps> = (props: IReactTableProps): JSX.Element => {
    const {
        rows,
        columns,
        disableSort,
        disableFilter,
        enableGrouping,
        groupColumnName,
        viewOnly,
        message,
        columnExtensions,
        sortingStateColumnExtensions,
        defaultSorting,
        onDeleteRow,
        enableSelection,
        enableSelectAll,
        onRowClick,
        totalCount,
        onPageChange,
        currentPage,
        pageSize,
        onSetPageSize,
        onCheckRow,
        onSelectGroup,
        // groupNameChecked,
        loading,
        defaultFilters,
        onSortingChange
    } = props;

    const [selection, setSelection] = React.useState<any>([])
    const [pageSizes] = React.useState([5, 10, 15]);
    const [sorting, setSorting ] = React.useState([])
    // const [pageSize, setPageSize] = React.useState(10);
    // console.log(onCheckRow, onSelectGroup)

    const setSortingMethod = (e : any) => {
        onSortingChange && onSortingChange(e)
        setSorting(e);
    }
    const handleSelectionChange = (row : any[]) => {
        if (onRowClick) {
            if (!enableSelection) {
                onRowClick(row[0]);
            }
        } else {
            setSelection(row);
        }
    }

    const selectCell = ({row, onToggle}: any) => {
        return (
            <MaterialUI.TableCell>
                <MaterialUI.Checkbox
                    icon={<FontAwesomeIcon icon={faMinusSquare} />}
                    checkedIcon={<FontAwesomeIcon icon={faCheckSquare} />}
                    checked={row.checked}
                    onClick={(e: any)=> {
                        e.stopPropagation();
                        onToggle();
                        onCheckRow && onCheckRow(row);
                    }}
                />
            </MaterialUI.TableCell>
        )
    }


const groupContentCell = ({row}: any) => {
    
    let isChecked = false;
    let countChecked = 0;
    let countItems = 0;
    rows.map(data => {
        if (data.name === row.value) {
            countItems++;
            if (data.checked) {
                isChecked = true;
                countChecked++;
            }
        }
    })
    
    if (!viewOnly) {
        return (
            <>
                <div style={{padding: '8.5px 15.5px', display:'table-cell', verticalAlign:'middle'}} > 
                    <MaterialUI.Checkbox
                        icon={<FontAwesomeIcon icon={faMinusSquare} />}
                        checkedIcon={<FontAwesomeIcon icon={faCheckSquare} />}
                        checked={isChecked}
                        onClick={e => {
                            e.stopPropagation();
                            onSelectGroup && onSelectGroup(row.value)
                        }}
                />
                </div>
                <div style={{padding: '8.5px 2px', display:'table-cell', verticalAlign:'middle'}} >
                    <span>{row.value}</span>
                </div>
                <div style={{padding: '8.5px 30px', display:'table-cell', verticalAlign:'middle'}} >
                    <span>{countChecked}/{countItems}</span>
                </div>
            </>
        )
    } else {
        return (
            <div style={{padding: '8.5px 2px', display:'table-cell', verticalAlign:'middle'}} >
                    <span>{row.value}</span>
            </div>
        )
    }
    
}

    const tableCell = ({ value, row, column }: any) => {
        if (column.name === 'action_delete') {
            return (
                <MaterialUI.TableCell style={{ padding: '0px 30px 8px 10px', textAlign: 'right' }}>
                    <span
                        onClick={() => {
                            onDeleteRow && onDeleteRow(row)
                        }}>

                        <FontAwesomeIcon
                            icon={faMinusCircle}
                        />
                    </span>

                </MaterialUI.TableCell>
            )
        } else {
            return (
                <MaterialUI.TableCell>
                    {value}
                </MaterialUI.TableCell>
            )
        }
    }


    return (
        <div style={{ padding: '10px 0' }}>
            <Paper>
                <Grid
                    rows={rows}
                    columns={columns}
                >
                    <PagingState
                        currentPage={currentPage}
                        onCurrentPageChange={onPageChange}
                        pageSize={pageSize}
                        onPageSizeChange={onSetPageSize}
                    />
                    <CustomPaging
                        totalCount={totalCount}
                    />
                    {!disableSort && onSortingChange ? (
                        <SortingState
                            defaultSorting={defaultSorting}
                            columnExtensions={sortingStateColumnExtensions}
                            sorting={sorting}
                            onSortingChange={setSortingMethod}
                        />
                    ) : !disableSort ? (
                        <SortingState
                          defaultSorting={[]}
                        />
                    ) : null}
                    {!viewOnly ? <SelectionState
                        selection={selection}
                        onSelectionChange={handleSelectionChange}
                    /> : null}
                    {enableGrouping && groupColumnName ? <GroupingState grouping={[{ columnName: groupColumnName }]} /> : null}
                    {!disableFilter ? 
                    <FilteringState 
                        defaultFilters={[]} 
                        filters={defaultFilters !== undefined ? defaultFilters : []}
                        onFiltersChange={props.onFilterChange}
                    /> : null}
                    {/* <IntegratedPaging /> */}
                    {!defaultFilters && !disableFilter ? <IntegratedFiltering /> : null}
                    {!disableSort ? <IntegratedSorting /> : null}
                    {enableGrouping ? <IntegratedGrouping /> : null}
                    <Table
                        columnExtensions={columnExtensions}
                        messages={{ noData: message }}
                        cellComponent={tableCell}
                    />
                    <TableHeaderRow
                        showSortingControls={!disableSort}
                        sortLabelComponent={SortLabel}
                    />
                    {/* <TableFixedColumns /> */}
                    <PagingPanel
                        pageSizes={pageSizes}
                    />
                    {!viewOnly ? 
                        <TableSelection 
                            highlightRow 
                            selectByRowClick
                            showSelectAll={enableSelectAll ? true : undefined} 
                            cellComponent={!loading ? selectCell : selectCell} 
                            showSelectionColumn={enableSelection}
                        /> : null}
                    
                    {enableGrouping ? 
                        <TableGroupRow 
                            contentComponent={groupContentCell}
                            iconComponent={groupIconContentCell}
                        ></TableGroupRow>: null}
                    {!disableFilter ? <TableFilterRow cellComponent={FilterCell} /> : null}
                </Grid>
                {
                    loading &&
                    <Loader />
                } 
            </Paper>
        </div>
    )
}

ReactTable.defaultProps = defaultProps;